import React from 'react';
import { HomeTabs, OrderStateCard, OrderArborTable} from '../component/home/Home';
import Modal from 'react-bootstrap/Modal';
import {ApiGetOrderList, ApiGetArborList } from '../api.js';
import './home.css';

class Home extends React.Component{
	constructor() {
		super();
		this.showModal = this.showModal.bind(this);
		this.closeModal = this.closeModal.bind(this);
		this.state = {
			modal_tips: false,
			orderList: [],
			arborList: [],
			currentOrder: '', //當前點擊刀具列表之order
		}
	}
	
	refresh() {
		const newOrderList = []
		const newArborList = []

		/* 
		ApiGetOrderList({})
		.then(res => {
			this.setState({ newOrderList: res.data })
			console.log(res.data)
		})
		.catch(err => {
			console.log("Axios error in ApiGetOrderList");
			console.log(err);
		})

		ApiGetArborList({})
		.then(res => {
			this.setState({ newArborList: res.data })
			console.log(res.data)
		})
		.catch(err => {
			console.log("Axios error in ApiGetArborList");
			console.log(err);
		})


		*/

		/*模擬資料*/
		//'運行中', '異常','停止'
		const orderStateArr =['idle', 'running', 'running_delay', 'paused', 'stopped', 'error', 'finished']
		
		//'領刀中', '正常','換刀', '待入庫'
		const arborStatusArr = ['idle', 'assigned_uncheck', 'assigned', 'installed', 'used']
		const orderAmount = parseInt(Math.random() * 10) + 1 
		const odderIdList = []
		
		console.log(orderAmount)
		for (let index = 0; index < orderAmount; index++) {
			const orderState = orderStateArr[parseInt(Math.random()*orderStateArr.length)]
			const arborState = arborStatusArr[parseInt(Math.random()*arborStatusArr.length)]
			let quantity = parseInt(Math.random() * 600000) + 300000
			let currentQuantity = parseInt(Math.random() * quantity)
			if (arborState == 'assigned_uncheck'){
				currentQuantity = quantity
			}else if (arborState == 'assigned'){
				currentQuantity = 0
			}
			
			
			odderIdList.push('cc' + index )
			newOrderList.push({
				order_name: "531NK-191000123_K800",
				product: "123",
				op: "OP03",
				time_start: "2020-02-10T08:43:48.726Z",
				quantity: quantity,
				current_quantity: currentQuantity,
				order_state: orderState, //idle, running, running_delay, paused, stopped, error, finished
				arbor_state: arborState, //idle, assigned_uncheck, assigned, installed, used 
				device_name: "CNC01",
				created_at: "2020-02-10T08:43:48.726Z",
				measure: 0,
				orderid: 'cc' + index 
			})
		}

		const arborAmount = parseInt(Math.random() * 50) + 50
		
		for (let index = 0; index < arborAmount; index++) {
			const life = parseInt(Math.random() * 3)*10 + 10
			const countLift = parseInt(Math.random() * life)

			newArborList.push({
				arbor_name: "A000-" + index,
				tool_modus: "test",
				count_lift: countLift,
				life: life,
				holder: "T" + index,
				order_name: odderIdList[parseInt(Math.random()*odderIdList.length)]
			})	
		}

		console.log(newOrderList)
		console.log(newArborList)
		this.setState({ 
			orderList: newOrderList,
			arborList: newArborList
		})
		
	}

	showModal(orderName) { 
		console.log(orderName)
		this.setState({
			modal_tips: true,
			currentOrder: orderName
		})
	}

	closeModal() {
		this.setState({
			modal_tips: false
		});
	}

	componentDidMount() {
		this.refresh()

		setInterval(async () => {
			this.refresh()
		}, 5000);
	}

  render(){
		const {
			orderList,
			arborList,
			currentOrder
		} = this.state
		
		const OrderStateCardList = orderList.map((order, index) =>
			<OrderStateCard 
			key={index}
			orderid={order.orderid}
			orderName = {order.order_name}
			deviceName = {order.device_name}
			op = {order.op}
			quantity = {order.quantity}
			currentQuantity = {order.current_quantity}
			orderState = {order.order_state}
			arborState = {order.arbor_state}
			showModal={(value) => this.showModal(value)}
			/>
		)

    return (
      <React.Fragment>
        <div className="index_area sys_area clearfix">
          <HomeTabs tab='1'/>
          <div className="tab-content tabs_content shadow-lg clearfix">	
						<div className="work_area clearfix">
							<ul className="pie_area list-unstyled mb-0 clearfix">
								{OrderStateCardList}
							</ul>
						</div>	
          </div>
        </div>
				<Modal className=" modal_style fade" size="lg" centered show={this.state.modal_tips} onHide={this.closeModal}>
					<Modal.Header>
						<Modal.Title as="h5"><b>刀具列表</b></Modal.Title>
					</Modal.Header>
					<Modal.Body>
						<div className="tableOutDiv">
							<OrderArborTable arborList={arborList} orderName={currentOrder}/>
						</div>
					</Modal.Body>
					<Modal.Footer className="d-flex justify-content-end">
						<button type="button" className="btn btn_common btn_add">前往刀桿清單</button>
						<button type="button" className="btn btn_common btn_cancel" onClick={this.closeModal}>取消</button>
					</Modal.Footer>
				</Modal>
      </React.Fragment>
    )
  }
}

class HomeArborAnalysis extends React.Component{
  render(){
    return (
      <React.Fragment>
        <div className="index_area sys_area clearfix">
          <HomeTabs tab='2'/>
          <div className="tab-content tabs_content shadow-lg clearfix">	
						<div className="work_area clearfix">
							<ul className="pie_area list-unstyled mb-0 clearfix">
								
							</ul>
						</div>	
          </div>
        </div>
      </React.Fragment>
    )
  }
}

export { Home, HomeArborAnalysis};
