import React from 'react';
import { Link } from 'react-router-dom';
import { Tabs, Table} from '../Component';
const $ = require('jquery');
$.DataTable = require('datatables.net');


class HomeTabs extends React.Component {
  getclass(tab) {
    if (this.props.tab === tab) {
      return "active";
    } else {
      return "";
    }
  }
  render() {
    return (
      <Tabs>
        <li className="nav-item">
          <Link to="/pages/home" className={"nav-link " + this.getclass('1')}>工單生產狀態</Link>
        </li>
        <li className="nav-item">
          <Link to="/pages/home_arbor_analysis" className={"nav-link " + this.getclass('2')}>刀具庫存分析</Link>
        </li>
      </Tabs>
    )
  }
}

class OrderStateCard extends React.Component {
  constructor() {
    super();
    this.state = {
      orderStateStr:{
        idle: '停止',
        running: '運行中', 
        running_delay: '異常',
        paused: '停止',
        stopped: '停止',
        error: '異常',
        finished: '停止'
      },
      arborStateStr:{
        idle: '換刀',
        assigned_uncheck: '待入庫', 
        assigned: '領刀中',
        installed: '正常',
        used: '正常'
      },
      viewOrderState: "",
      viewArborState: ""
    }
  }

  cardAnimate(){
    //原始 demo 效果 code
    const jsClassName = this.props.orderid //暫時用來區別各card的className，避免因名稱相同重複執行
    const state = this.state
    $('.' + jsClassName).each(function () {
      $(this).prop('Counter',0).animate({
        Counter: $(this).text()
      }, {
        duration: 1000,
        easing: 'swing',
        step: function (now, fx) {
          $(this).parents('.js_number').removeClass(function (index, css) {
            return (css.match (/\bpct_\S+/g) || []).join('');
          });
          if(state.viewArborState === '待入庫'){
            $(this).parents('.js_number').addClass('pct_orange_0');
          }else{
            if ($(this).parents('.js_number')) {
              if (state.viewArborState === '換刀'){
                
                $(this).parents('.js_number').addClass('pct_orange_' + Math.ceil(now));
              }else{
                $(this).parents('.js_number').addClass('pct_green_' + Math.ceil(now));
              }
            }
          }
          $(this).text(number_format(Math.ceil(now), 0, ','));
        }
      });
    });
  }
  handleClickModal() {
    console.log('我按了')    
    this.props.showModal(this.props.orderid) //模擬資料orderName是重複的固先用orderid替代
  }
 
  componentDidMount() {
    this.setState({
      viewOrderState: this.state.orderStateStr[this.props.orderState],
      viewArborState: this.state.arborStateStr[this.props.arborState]
    }, () => { this.cardAnimate() })
  }
  componentDidUpdate(prevProps){
    if (this.props.currentQuantity !== prevProps.currentQuantity) {
      this.setState({
        viewOrderState: this.state.orderStateStr[this.props.orderState],
        viewArborState: this.state.arborStateStr[this.props.arborState]
      }, () => { this.cardAnimate() })
    }
  }

  render() {
    const { 
      orderid,
      orderName,
      deviceName,
      op,
      quantity,
      currentQuantity,
    } = this.props

    const {
      viewOrderState,
      viewArborState
    } = this.state

    const percentage = Math.round(currentQuantity/quantity*100,0)
    let cardHeaderClass = ''
    if (viewArborState === '正常'){
      cardHeaderClass = 'greem'
    }else if(viewArborState === '換刀'){
      cardHeaderClass = 'orange'
    }else{
      cardHeaderClass = 'gray'
    }

    const arborArea =  viewArborState !== '換刀'?
      <span className="w-100  mx-3 my-px" >
        <h4 className="my-2 text-center"><b>{viewArborState}</b></h4>
        <h6 className="mb-2 text-center">刀具狀態</h6>
      </span> 
      : 
      <span className="w-100  mx-3 orangeButton" modaltoshow="modal_tips" onClick={() => this.handleClickModal()}>
        <h4 className="my-2 text-center" modaltoshow="modal_tips"><b modaltoshow="modal_tips" >{viewArborState}</b></h4>
        <h6 className="mb-2 text-center" modaltoshow="modal_tips">刀具狀態</h6>
      </span>
    
    return (
      <React.Fragment>
        <li>
          <div className="pie_inner card rounded-0 clearfix border">
            <div className={"card-head " + cardHeaderClass}>
              <div className="d-flex justify-content-start">
                <h5 className="mt-0"><b>{orderName}</b></h5>
              </div>
              <div className="d-flex justify-content-between">
                <h6 className="mb-0"><b>{deviceName}</b></h6>
                <h6 className="mb-0"><b>{op}</b></h6>
              </div>
            </div>
            <div className="card-body px-0 pb-3">
              <div className="pie_data top clearfix">
                <div className="d-flex justify-content-evenly">
                  <span className="w-100">
                    <h4 className="mt-0 text-center"><b className={orderid}>{currentQuantity}</b></h4>
                    <h6 className="mb-0 text-center">生產數量</h6>
                  </span>
                  <span></span>
                  <span className="w-100">
                    <h4 className="mt-0 text-center"><b className={orderid}>{quantity}</b></h4>
                    <h6 className="mb-0 text-center">目標數量</h6>
                  </span>
                </div>
              </div>
              <div className="d-flex justify-content-center">
                <div className="pie_chart rounded js_number d-flex justify-content-center align-items-center rounded-circle">
                  <div className="pie_info">
                    <h4 className="text-center mt-0"><span><b className={orderid}>{percentage}</b><small>%</small></span></h4>
                    <h6 className="text-center m-0"><span className="js_date">生產進度</span></h6>
                  </div>
                </div>
              </div>
              <div className="pie_data bottom clearfix">
                <div className="d-flex justify-content-around">
                  <span className="w-100 mx-3 my-2px">
                    <h4 className="my-2 text-center"><b>{viewOrderState}</b></h4>
                    <h6 className="mb-2 text-center">運行狀態</h6>
                  </span>
                  {arborArea}
                </div>
              </div>
            </div>
          </div>
        </li>
      </React.Fragment>
    )
  }
}



class OrderArborTable extends React.Component {
  constructor() {
    super();
    this.state = {
      data: []
    }
  }
  
  loadData() {
    const data = []

    this.props.arborList.forEach(arbor => {
      if (arbor.order_name === this.props.orderName){
        data.push({
          arborName: arbor.arbor_name,
          toolModus: arbor.tool_modus,
          countLift: arbor.count_lift,
          life: arbor.life,
          holder: arbor.holder,
        })
      }
    })
    console.log(this.props.orderName)
    this.setState({ data: data });
  }

  componentDidMount() {
    this.loadData();
  }

  render() {
    const columns = [
      {
        Header: '刀桿ID',
        accessor: 'arborName'
      },
      {
        Header: '刀具形式',
        accessor: 'toolModus'
      },
      {
        Header: '可使用次數',
        accessor: 'countLift',
        Cell: ({ cell: { value } }) => {
          let textColor = ""
          if (value === 0){
            textColor = 'textRed'
          }else if(value <= 5){
            textColor = 'textOrange'
          }
          return (
            <React.Fragment>
              <span className={textColor}>{value}</span>
            </React.Fragment >
          )
        }
      },
      {
        Header: '壽命',
        accessor: 'life'
      },
      {
        Header: '刀杯',
        accessor: 'holder'
      },
    ];

    return (
      <Table columns={columns} data={this.state.data} />
    )
  }
}

//原始 demo 效果 code
function number_format(number,bit,sign,gapnum) {
  bit    = arguments[1] ? arguments[1] : 2 ;
  sign   = arguments[2] ? arguments[2] : '' ;
  gapnum = arguments[3] ? arguments[3] : 3 ;
  let str    = '' ;

  number     = number.toFixed(bit);
  let realnum    = number.split('.')[0];
  const decimal    = number.split('.')[1];
  let realnumarr = realnum.split('');

  for(var i=1;i<=realnumarr.length;i++){
      str = realnumarr[realnumarr.length-i] + str ;
      if(i%gapnum === 0 && sign !== ''){
          str = sign+str;
      }
  }

  str = (realnum.length%gapnum === 0) ? str.substr(1) : str;

  realnum = decimal > 0 ? str+'.'+decimal : str;

  return realnum;
}




export { HomeTabs, OrderStateCard, OrderArborTable};

